.scroll_navi {
	position: fixed;
	top: 50%;
	transform: translateY(-50%);
	right: 0px;
	z-index: 4;
	li {
		width: 8px;
		height: 8px;
		background: #bbb;
		border-radius: 50%;
		margin: 40px;
		cursor: pointer;
		transition: 0.5s;
		&.on {
			width: 10px;
			height: 10px;
			background: #555;
		}
	}
}

@media screen and (max-width: $tablet) {
	.scroll_navi {
		display: none;
	}
}
